@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
body {
    margin: 0;
    padding: 0;
}
.app {
    font-family: 'Kanit', sans-serif;
}

.home {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bg-img {
  width: 100%;
  height: 100vh;
  position: relative;
  margin: 0;
}

.home-body {
  position: absolute;
  border: solid 1px white;
  width: 55%;
  margin-top: 200px;
  background-color: rgba(255, 255, 255, 0.185);
}

.balance {
  color: white;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.26);
  color: white;
  margin: 1rem;
  border-radius: 5px;
  padding: .5rem;
}

.home-welcome-msg {
  font-size: 80px;
  color: white;
  text-align: center;
}

.home-ques {
  text-align: center;
  font-style: italic;
  color: white;
  font-size: 16px;
}

.home-btns {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  margin: 2rem auto 2rem auto;
}

.home-btn {
  background-color: yellow;
  font-size: 16px;
  width: 80px;
  height: 40px;
  font-family: "Kanit", sans-serif;
  border-radius: 5px;
  cursor: pointer;
}

form * {
  font-family: "Kanit", sans-serif;
}

form button {
  margin: 0 10px;
}

.form-highlight {
  color: green;
  font-family: monospace;
}

.check-avail {
  font-size: 13px;
}

.check-avail-s {
  color: green;
}

.check-avail-f {
  color: red;
}

.staff {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin: 20px;
}

.staff-heading {
  font-size: 50px;
  border-bottom: 1px solid black;
  margin-bottom: 20px;
}

.notification {
    border: 1px solid gray;
    margin: 20px;
    padding: 10px;
    border-radius: 5px;
    background-color: rgba(255, 255, 0, 0.164);    
}

.noti-highlight {
    color: rgb(26, 187, 26);
}

